.main-form {
    background-color: #A9C5B9;
    border-radius: 5px;
    text-align: right;
    padding: 3vh 5vw 3vh 5vw;
    z-index: 100;

    width: 70vw;
    margin: 0 auto;
    margin-bottom: 5vh;

}

.input-block {
    margin-bottom: 1vh;

    display: flex;
    flex-direction: column;
}

.form-input-lable {
    font-weight: 500;
    font-size: 14px;

}

.form-input {
    border: 1px solid #000000;
    border-radius: 5px;
    height: 4vh;
    resize: none;
    padding: 0;
    flex-grow: 1;
    text-align: right;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 500;
    padding-right: 2vw;

}

.submit-button {
    background-color: #45526E;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 500;
    color: #FFFFFF;
    border: none;
    border-radius: 5px;
    height: 37px;
    width: 100%;
}